import React, { Component } from 'react';
import {connect} from 'react-redux';
import { fetchSmurfs, addSmurf } from '../actions';
import PostForm from './PostForm';
import "../App.css";



class Smurfies extends Component {
   componentDidMount() {
     this.props.fetchSmurfs();

   }

   render() {
  
      return (



      <div className="container">

       
      <div className="header">
         <h1>Smurf Builder</h1>
      </div>

      <PostForm addSmurf={this.props.addSmurf}  />

      <div className="smurfrow">

      {this.props.smurfs.map(x => {
         return (    

          
         
         <div id={x.id} className="asmurf">

          <h2> Name: {x.name} </h2>
            <h3>Age:  {x.age}  </h3>
           Height:  {x.height}

            </div>

      
            )
        

      })}
          </div>
     
          </div>
            


   

      )}}

 

 




const mapStateToProps = state => {
   return {isFetching: state.isFetching, smurfs: state.smurfs};
};

export default connect(mapStateToProps, {fetchSmurfs: fetchSmurfs, addSmurf})(Smurfies);