import axios from 'axios';

export const FETCHING_SMURFS = "FETCHING_SMURFS";
export const SMURF_FETCH_SUCESS = "SMURF_FETCH_SUCESS";


export const fetchSmurfs = () => {

   return function(dispatch) {
     dispatch({ type: FETCHING_SMURFS });
     axios
       .get("http://localhost:3333/smurfs")
       .then(res => {
          console.log(res.data[4]);
            dispatch({ type: SMURF_FETCH_SUCESS, payload: res.data })
       })
       .catch(err => console.error(err));
   };
 };

 export const ADD_SMURF = 'ADD_SMURF';
 export const ADD_SMURF_SUCCESS = 'ADD_SMURF_SUCCESS';
 export const ADD_SMURF_FAIL = 'ADD_SMURF_FAIL';


 
 export const addSmurf = smurfie => dispatch => {
  dispatch({ type: ADD_SMURF });

  axios
    .post('http://localhost:3333/smurfs', smurfie)
    .then(res => {
    
      dispatch({ type: ADD_SMURF_SUCCESS, payload: res.data });
      console.log(res.data[3])
    })
    .catch(err => {
      dispatch({ type: ADD_SMURF_FAIL, payload: err });
    });
};
 
 