import {
   FETCHING_SMURFS, 
   SMURF_FETCH_SUCESS, 
   ADD_SMURF,
   ADD_SMURF_SUCCESS,
   ADD_SMURF_FAIL
} from '../actions';

const intialState = {
   isFetching: false,
   error: "",
   smurfs: []
};


const reducer = (state = intialState, action) => {
   switch(action.type) {
   case FETCHING_SMURFS:
      return {
         ...state,
         isFetching: true,
         error: false
      };
 
   case SMURF_FETCH_SUCESS: 
         return {
         ...state,
         smurfs: action.payload,
         error: false,
         isfetching: false
            };

   case ADD_SMURF:
      return{
         ...state,
         isfetching: true,
         error: false 
      };

      case ADD_SMURF_SUCCESS:
      return {
        ...state,
        smurfs: action.payload,
        isFetching: false,
        error: false
      };
    case ADD_SMURF_FAIL:
      return {
        ...state,
        isFetching: false,
        error: true
      };
      
            
   default:
      return state; 
   }
}

export default reducer;