import React, { useState } from 'react';



const PostForm = ({addSmurf}) => {
    const [name, setName] = useState('');
    const [age, setAge] = useState('');
    const [height, setHeight] = useState('');


  const onSubmit = e => {
      e.preventDefault();
      addSmurf({
          name,
          age,
          height
      });

      setName('');
      setAge('');
      setHeight('')
  };



    return (


        <div className="form">

            <h3>Add a Smurf to start building your family!</h3>
     

            <form className="form" onSubmit={onSubmit}>

                <input
                    className="field"
                    component="input"
                    type="text"
                    name="name"
                    placeholder="Enter name"
                    onChange={e => setName(e.target.value)}
                    value={name}
                />
             
            
                <input
                    className="field"
                    component="input"
                    type="text"
                    name="Age"
                    placeholder="Enter Age"
                    onChange={e => setAge(e.target.value)}
                    value={age}
                />
               

          

                <input
                    className="field"
                    component="input"
                    type="text"
                    name="height"
                    placeholder="Enter height"
                    onChange={e => setHeight(e.target.value)}
                    value={height}
                />

             
<br />

                <button
                    type="submit"
                >
                    Add smurf
                </button>
            </form>
     <br />


            </div>
        
)
}


      



export default PostForm;